import React from 'react';
import { NavLink } from 'react-router-dom';

const Header = () => (
    <header>
        <span className="icn-logo"><i className="material-icons"></i></span>
            <ul className="main-nav">
                <li><NavLink exact to="/">Home</NavLink></li>

            </ul> 
    </header>
);

export default Header;