import React from 'react';

import axios from 'axios';

export default class PersonList extends React.Component {
  state = {
    city: []
  }

  componentDidMount() {
    axios.get(`https://www.metaweather.com/api/location/search/?query=louisville`)
      .then(res => {
        const city = res.title;
        this.setState({ city });
        console.log({city})
      })
  }

  render() {
    return (
      <ul>
        <li>{this.state.city}</li>
      </ul>
    )
  }
}